
import main from '../assets/freightPage/FreightBusinessMain.jpg'
import servicePoint from '../assets/freightPage/pallet-svgrepo-com.svg'
import transitDepos from '../assets/freightPage/pushcart-svgrepo-com.svg'
import city from '../assets/freightPage/city-svgrepo-com.svg'
import truck from '../assets/freightPage/truck-svgrepo-com.svg'

import Trucks from '../components/trackroutestbl'

const FreightBusiness = () => (
    <>
<section class="mw5 mw7-ns center bg-moon-gray pa3 ph5-ns">

{/* <div id="cell2"className="cell pa3 ma2 grow center" >Freight Business</div> */}


<img className="" src={main}  />
    
        <article className="pa3 pa5-ns">
            <h1 className="f3 f1-m ">FREIGHT BUSINESS overview</h1>
            <p className="measure lh-copy center">
                SF Freight focuses on solving problems under specific logistics business scenarios such as customers
                factory warehouses, storage of warehouses, store warehouses, e-commerce and daily-life large parcels,
                focuses on actual business results related to the customers to build problem-solving capacity,
                provide product and solution with logistics as the core. It is customers highly-efficient freight leading partner.
            </p>
        </article>
    

    <article class="pa3 pa5-ns">
            <h1 class="f4 bold center mw6">Heavy Freight Services</h1>
                <ul class="list pl0 ml0 center mw6 ba b--light-blue bg-light-gray br2">
                    <li className="ph3 pv3 bb b--light-silver">Heavy Freight Package</li>
                    <li className="ph3 pv3 bb b--light-silver">Large Size LTL</li>
                    <li className="ph3 pv3 bb b--light-silver">City Special Delivery (enterprise/individual relocation, store distribution business)</li>
                    <li className="ph3 pv3 bb b--light-silver">Standard LTL</li>
                    <li className="ph3 pv3 bb b--light-silver">FTL Direct Delivery</li>
                </ul>
        </article>

        <ul className="list pl0 mt0 measure center">
            <li
                className="flex items-center lh-copy pa3 ph0-l bb b--black-10">
                <img className="w2 h2 w3-ns h3-ns" src={servicePoint} />
                <div className="pl3 flex-auto">
                    <span className="f6 db black-70">1500</span>
                    <span className="f6 db black-70">freight service points</span>
                </div>
              
            </li>
            <li
                className="flex items-center lh-copy pa3 ph0-l bb b--black-10">
                <img className="w2 h2 w3-ns h3-ns" src={transitDepos}  />
                <div className="pl3 flex-auto">
                    <span className="f6 db black-70">185</span>
                    <span className="f6 db black-70">freight transit depots</span>
                </div>
               
            </li>
            <li
                className="flex items-center lh-copy pa3 ph0-l bb b--black-10">
                <img className="w2 h2 w3-ns h3-ns" src={city}  />
                <div className="pl3 flex-auto">
                    <span className="f6 db black-70">365</span>
                    <span className="f6 db black-70">major cities and areas covered</span>
                </div>
                
            </li>
            <li
                className="flex items-center lh-copy pa3 ph0-l bb b--black-10">
                <img className="w2 h2 w3-ns h3-ns" src={truck}  />
                <div className="pl3 flex-auto">
                    <span className="f6 db black-70">2911</span>
                    <span className="f6 db black-70">freight long-haul routes</span>
                </div>
               
            </li>
        </ul>

    <Trucks />

    </section>
        
    </>
)

export default FreightBusiness