
import main from '../assets/freightPage/SFAirlines.jpg'
import plane from '../assets/freightPage/plane-svgrepo-com.svg'
import warehouse from '../assets/freightPage/warehouse-svgrepo-com.svg'
import routes from '../assets/freightPage/flight-route-traveling-svgrepo-com.svg'
import flights from '../assets/freightPage/in-flight-svgrepo-com.svg'

import AirfreightTable from '../components/airfreighttbl'


const AirFreight =() => (
    <>

<section class="mw5 mw7-ns center bg-light-gray pa3 ph5-ns">

{/* <div id="cell3"className="cell pa3 ma2 grow center" >Express Business</div> */}

<img className="" src={main}  />


 
    
        <article className="pa3 pa5-ns">
            <h1 className="f3 f1-m ">SF AIRLINES</h1>
            <p className="measure lh-copy center">
            In 2003, SF became the first private Chinese express company to charter night flights.
            In 2009, SF Airlines, a subsidiary of SF, officially made its maiden flight, making SF as the first private courier company with its own all-cargo aircraft.
            As of  the end of 2020, it had 2110 all-cargo and commercial flight routes, making an average of 3,743 flights daily.
            As of  the end of 2020, it had 75 all-cargo aircraft  serving SF (61 of which were owned by SF Airlines). 

            </p>
        </article>
    



        <ul className="list pl0 mt0 measure center">
            <li
                className="flex items-center lh-copy pa3 ph0-l bb b--black-10">
                <img className="w2 h2 w3-ns h3-ns" src={plane} />
                <div className="pl3 flex-auto">
                    <span className="f6 db black-70">75</span>
                    <span className="f6 db black-70">all-cargo aircraft</span>
                </div>
              
            </li>
            <li
                className="flex items-center lh-copy pa3 ph0-l bb b--black-10">
                <img className="w2 h2 w3-ns h3-ns" src={routes}  />
                <div className="pl3 flex-auto">
                    <span className="f6 db black-70">2110</span>
                    <span className="f6 db black-70">all-cargo and commercial flight routes</span>
                </div>
               
            </li>
            <li
                className="flex items-center lh-copy pa3 ph0-l bb b--black-10">
                <img className="w2 h2 w3-ns h3-ns" src={flights}  />
                <div className="pl3 flex-auto">
                    <span className="f6 db black-70">3743</span>
                    <span className="f6 db black-70">daily average flights</span>
                </div>    
            </li>
        </ul>

        <AirfreightTable />

    </section>

    </>
)

export default AirFreight