
import main from '../assets/freightPage/ExpressBusiness.jpg'
import plane from '../assets/freightPage/plane-svgrepo-com.svg'
import warehouse from '../assets/freightPage/warehouse-svgrepo-com.svg'
import truck from '../assets/freightPage/truck-svgrepo-com.svg'
import route from '../assets/freightPage/route-svgrepo-com.svg'


const ExpressBusiness =() => (
    <>

<section class="mw5 mw7-ns center bg-light-gray pa3 ph5-ns">

{/* <div id="cell3"className="cell pa3 ma2 grow center" >Express Business</div> */}

<img className="" src={main}  />


 
    
        <article className="pa3 pa5-ns">
            <h1 className="f3 f1-m ">EXPRESS BUSINESS overview</h1>
            <p className="measure lh-copy center">
            SF is the first company in the Chinese express delivery industry to own a cargo airline, with a fleet of 75 all-cargo aircraft as of the end of 2020 transporting millions of express shipments every day.
SF has 10 hub-level transit depots, service points at 39 aviation and railway stations (excluding service points sharing sites with transit depots), and 147 sub-district transit depots (excluding freight and Shunxin Express transit depots), of which 121 transit depots have adopted an automatic sorting system, ensuring that shipments are delivered on time and safely arrive at their destinations.
SF is committed to developing cutting-edge customer service systems, and responding promptly to customers' needs; and to building a smart and digital service management platform that provides customers with professional, efficient and user-friendly services. SF has 4 separate call centers, and over 1 million customer enquiries are served on a daily basis. Customers have 24/7 access to internet-based self-service via WeChat mini-program, WeChat official account, the Companys official website, the SF Credit Account Management Platform and mobile apps. In addition, the speed of customer service response and customer experience have been greatly improved through various service platforms and intelligent tools.
SF is the first Chinese enterprise to have an online centralized mobile service system, with all couriers equipped with high-tech hand-held terminals. In addition, SF has successively developed multiple intelligent systems for management of the entire express shipment life cycle, online docking of key accounts, and resource scheduling and monitoring to ensure the safety of express shipments, thereby providing customers with better service.
            </p>
        </article>
    



        <ul className="list pl0 mt0 measure center">
            <li
                className="flex items-center lh-copy pa3 ph0-l bb b--black-10">
                <img className="w2 h2 w3-ns h3-ns" src={plane} />
                <div className="pl3 flex-auto">
                    <span className="f6 db black-70">75</span>
                    <span className="f6 db black-70">all-cargo aircraft</span>
                </div>
              
            </li>
            <li
                className="flex items-center lh-copy pa3 ph0-l bb b--black-10">
                <img className="w2 h2 w3-ns h3-ns" src={warehouse}  />
                <div className="pl3 flex-auto">
                    <span className="f6 db black-70">374</span>
                    <span className="f6 db black-70">warehouses of different types</span>
                </div>
               
            </li>
            <li
                className="flex items-center lh-copy pa3 ph0-l bb b--black-10">
                <img className="w2 h2 w3-ns h3-ns" src={truck}  />
                <div className="pl3 flex-auto">
                    <span className="f6 db black-70">58 000+</span>
                    <span className="f6 db black-70">long-haul/branch route vehicles</span>
                </div>
                
            </li>
            <li
                className="flex items-center lh-copy pa3 ph0-l bb b--black-10">
                <img className="w2 h2 w3-ns h3-ns" src={route}  />
                <div className="pl3 flex-auto">
                    <span className="f6 db black-70">130 000+</span>
                    <span className="f6 db black-70">transportation long-haul/branch routes</span>
                </div>
               
            </li>
        </ul>

    </section>

    </>
)

export default ExpressBusiness