
import banner from '../assets/banner1400.png'

const Banner = () => {
    return (
        <>
            <figure class="mainbanner">
                <img id="mainbanner" src={banner} alt="mainbanner"/>
                <figcaption>
                    <h1><span class="slogan">Deliver</span> on our every promise</h1>
                </figcaption>
            </figure>
        </>
        
    )
}

export default Banner