import logo from '../SFlogo.svg'
import './styles.scss'
import { Link } from 'react-router-dom'
import banner from '../assets/banner1400.png'

const Nav = () => {
    return (
        <>
        <header className="w-100 ph2 pv2 pv3-ns ph3-m ph3-l">
            <img src={logo} className="App-logo" alt="logo" style={{'height': '60px'}}/>
            <nav id='nav' className="f6 fw6 ttu ">
                <div id="navlinks" className='pa2'>
                    <Link to='/' className="link dim white dib mr3">Home</Link>
                    <Link to='/about' className="link dim white dib mr3">About</Link>
                    <Link to='/projects' className="link dim white dib mr3">Projects</Link>
                    <Link to='/contact' className="link dim white dib mr3">Contact</Link>
                    {/* <Link to='/profile' className="link dim white dib mr3">Profile</Link> */}
                    {/* <Link to='/register' className="link dim white dib mr3">Register</Link> */}
                </div>
            </nav>
                    
        </header>  
        </>
        
    )
}

export default Nav


   
        
     
    
